@import url('https://fonts.googleapis.com/css2?family=Dongle:wght@300;400;700&family=Noto+Sans+KR:wght@300;400;500;700;900&family=Roboto:wght@100;300;400;500&display=swap');

html,body{margin:0;}
figure{margin:0;}
h1,h2,h3,h4,h5,h6,p{margin:0}
img{vertical-align: middle;}
a{text-decoration:none;}
*{box-sizing:border-box; list-style:none;}
ul,li{padding-left:0;}
table{border-collapse: collapse;}
*{font-family: 'Noto Sans KR', sans-serif;}

.city_wrap {
    background-color: antiquewhite;
    width:100%;}
.city_wrap h2{text-align: center;
            font-size: 36px;
        color: rgb(80, 78, 78);}
.city_wrap ul{
    padding: 0 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.city_wrap ul li{
    margin: 10px;
    cursor: pointer;
}
.city_wrap ul li:hover{
    color:coral;
}
.city_wrap ul li.active{
    color:coral;
}

.city_wrap ul li{
    font-size: 16px;
    font-weight: bold;
}

.chart_wrap{
    display: flex;
    justify-content: space-between;
}
.chart_wrap .graph{width:60%;}
.chart_wrap .donut{
    position: relative;
    width:60%}
.chart_wrap .mini{
    position: absolute;
    left:27%; top:22%;
    font-size: 26px;
}




table{
    text-align: center;
    padding:10px 16px;
    width: 1800px;
    margin: 200px auto;
    /* border: 1px solid #ddd; */
}
thead{
    border-top: 2px solid rgb(38, 154, 231);
    border-bottom: 2px solid rgb(38, 154, 231);
}
thead th{
    
    font-size: 24px;
    line-height: 1.5em;
    font-weight: 600;
    letter-spacing: -0.048em;
    background: #f7faff;
    border: 1px solid #ddd;
}
tbody td{
    border: 1px solid #ddd;
    padding : 20px;
    font-size:20px;
}